(function() {
    'use strict';

    angular
        .module('frispo')
        .directive('eventEdit', eventEdit);

    /** @ngInject */
    function eventEdit(events) {
        var directive = {
            restrict: 'EA',
            templateUrl: 'app/pages/events/edit/edit.html',
            controller: EventEditController,
            controllerAs: 'eventEdit',
            link: EventEditLink,
            bindToController: true,
            replace: true,
            scope: {
                event: '=eventEdit'
            }
        };

        return directive;

        /** @ngInject */
        function EventEditController(models, matches, $uibModal, $q, utils, $window) {
            var vm = this;
            vm.models = models;
            vm.pointsOptions = utils.scores();
            vm.eventTypes = utils.eventTypes();
            vm.eventFormulas = utils.eventFormulas();
            vm.even_flg_subscribe = [{
                id: 0,
                value: "No"
            }, {
                id: 1,
                value: "Si"
            }];

            vm.setLoading = function(res) {
                vm.loading = true;
                return $q.resolve(res);
            }

            vm.unsetLoading = function(res) {
                vm.loading = false;
                return $q.resolve(res);
            }

            vm.submit = function() {
                return vm.setLoading(vm.form)
                    .then(events.getTokenS3)
                    .then(function(data) {
                        var angularFile = angular.element('#eventEditForm' + vm.event.evenId + '-evenUrlLogo');
                        var file = angularFile[0].files[0];
                        if (file) {
                            var deferred = $q.defer();
                            var credentials = {
                                accessKeyId: data.data[0].keyid,
                                secretAccessKey: data.data[0].secretid,
                                sessionToken: data.data[0].session_token
                            };
                            AWS.config.update(credentials);
                            AWS.config.region = 'eu-west-1';
                            AWS.config.sslEnabled = true;
                            var bucket = new AWS.S3({
                                params: {
                                    Bucket: 'com.codeploy.frispo.image.events'
                                }
                            });
                            var fileName = "";
                            if (file) fileName = angularFile[0].files[0].name;
                            var ts = new Date().getTime();
                            var fileNameEscape = fileName.replace(/\s+/g, '_');
                            vm.form.evenUrlLogo = ts + "-" + fileNameEscape;


                            if (file) {
                                var params = {
                                    Key: '' + ts + "-" + fileNameEscape,
                                    ContentType: file.type,
                                    Body: file
                                };
                                bucket.upload(params, function(err, data) {
                                    if (data) {
                                        deferred.resolve(data);
                                    } else if (err) {
                                        deferred.reject(err);
                                    }
                                })
                            } else {
                                deferred.reject(false);
                            }
                            return deferred.promise;
                        }
                    })
                    .then(function() {
                        return events.form2Event(vm.form);
                    })
                    .then(function(eventData) {
                        return events.update(vm.event.evenId, eventData);
                    })
                    .then(function() {
                        var span = angular.element('#' + vm.event.evenId + '-upload-file-info');
                        span.html('');
                    })
                    .finally(vm.unsetLoading);
            }

            vm.publish = function() {
                return vm.setLoading()
                    .then(function() {
                        return events.update(vm.event.evenId, {
                            even_stato_evento: 1
                        });
                    })
                    .finally(vm.unsetLoading);
            }

            vm.unpublish = function() {
                return vm.setLoading()
                    .then(function() {
                        return events.update(vm.event.evenId, {
                            even_stato_evento: 0
                        });
                    })
                    .finally(vm.unsetLoading);
            }

            vm.loadtmp = function() {
                return vm.setLoading()
                    .then(function() {
                        var span = angular.element('#' + vm.event.evenId + '-upload-file-info');
                        span.html('Immagine caricata, premere Salva per confermare');
                    })
                    .finally(vm.unsetLoading);
            }

            vm.showLogo = function(linkLogo) {
                $window.open(linkLogo, '_blank');
            }

            vm.recreateCalendar = function() {
                $uibModal.open({
                        templateUrl: 'app/pages/events/edit/recreateCalendar.html',
                        controller: 'EventRecreateCalendarController',
                        controllerAs: 'eventRecreateCalendar',
                        windowClass: 'modal-warning'
                    }).result
                    .then(function(res) {
                        if (res === true) {
                            return vm.setLoading(vm.event.evenId)
                                .then(matches.createCalendar)
                                .then(function() {
                                    return events.show(vm.event.evenId);
                                })
                                .finally(vm.unsetLoading);
                        }
                    })

            }
        }

        function EventEditLink(scope, el, attrs, vm) {

            el.find('.accordion-body').on('show.bs.collapse', function() {
                events.show(vm.event.evenId);
                events.listRegions();
            });

            vm.cancel = function() {
                el.find('.accordion-body').collapse('hide');
            }

            scope.$watch('eventEdit.event.evenCurrentDay', function(newVal, oldVal) {
                if (angular.isDefined(newVal) && newVal !== oldVal) {
                    vm.form.currentDay = vm.event.evenCurrentDay;
                }
            });
        }
    }

})();

