(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('cardMatchShow', cardMatchShow);

  /** @ngInject */
  function cardMatchShow() {
    var directive = {
      restrict: 'EA',
      templateUrl: 'app/pages/cardMatch/show/show.html',
      controller: ScorerMatchShowController,
      controllerAs: 'cardMatchShow',
      bindToController: true,
      replace: true,
      link: ScorerMatchShowLink,
      scope: {
        cardMatch: '=cardMatchShow',
        config: '=',
        cardMatchPlayerId: '=cardMatchPlayerId',
        cardMatchTeamId: '=cardMatchTeamId'
      }
    };

    return directive;

    /** @ngInject */
    function ScorerMatchShowController($uibModal, models, utils, matches) {
      var vm = this;
      vm.scores = utils.scores();

      vm.submit = function (){
        if(vm.cardMatch.cardId){
          return matches
            .form2CardMatch(vm.form)
            .then(function (cardData) {
              return matches.updateEventCard(
                vm.cardMatch.matcId,
                vm.cardMatchPlayerId,
                vm.cardMatch.cardId,
                vm.cardsKey,
                vm.cardMatchKey,
                cardData
              );
            })
        }
        else if(vm.cardMatch.amenId){
          return matches
            .form2AmendMatch(vm.form)
            .then(function (cardData) {
              return matches.updateEventAmend(
                vm.cardMatch.matcId,
                vm.cardMatchTeamId,
                vm.cardMatch.amenId,
                vm.cardsKey,
                vm.cardMatchKey,
                cardData
              );
            })
        }
      }

      vm.destroy = function() {

        $uibModal.open({
            templateUrl: 'app/pages/cardMatch/destroy/destroy.html',
            controller: 'CardMatchDestroyController',
            controllerAs: 'cardMatchDestroy',
            windowClass: 'modal-danger'
          }).result
          .then(function (res) {
            if(res === true){
              if(vm.cardMatch.cardId){
                matches.destroyEventCard(vm.cardMatch.matcId, vm.cardMatchPlayerId, vm.cardMatch.cardId, vm.config.cardsKey, vm.config.cardMatchKey);
              }
              else if(vm.cardMatch.amenId){
                matches.destroyEventAmend(vm.cardMatch.matcId, vm.cardMatchTeamId, vm.cardMatch.amenId, vm.config.cardsKey, vm.config.cardMatchKey);
              }
            }
          })
      }

      vm.models = models;
    }

    /** @ngInject */
    function ScorerMatchShowLink(scope, el, attrs, vm){
      el.hover(
        function () {
          scope.$apply(function () {
            vm.showControls = true;
          })
        },
        function () {
          scope.$apply(function () {
            vm.showControls = false;
          })
        }
      );
    }
  }

})();

