(function() { 'use strict';

  angular.module('frispo')
    .controller('TeamsController', TeamsController);

  /** @ngInject */
  function TeamsController(models, $scope, teams, $q, $uibModal, objToCsv) {

    var vm = this; vm.models = models; vm.loading = false;

    vm.getCurrentEvent = function() { return models.currentEvent || null; }

    // watch for changes in models.currentEvent and load
    // team and temp team info if current event has changed
    $scope.$watch(vm.getCurrentEvent, function(currentEvent) {
      if (!vm.loading && currentEvent &&
         (!currentEvent.teams || !currentEvent.tempTeams)) {
        vm.setLoading(currentEvent.evenId)        // Set a spinning loader on the page
          .then(teams.list)                       // List the teams of the event
          .then(teams.listTemp)                   // List the temp teams of the event
          .finally(vm.unsetLoading)               // Remove the loader from the page
      }
    }, true);     // use object equality and not memory address

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }

    vm.createNewTeam = function () {
      $uibModal.open({
          templateUrl: 'app/pages/teams/create/create.html',
          controller: 'TeamCreateController',
          controllerAs: 'teamCreate',
          windowClass: 'modal-success'
        })
        .result.then(function (res) {
          if(angular.isDefined(res)){
            vm.setLoading(res)
              .then(teams.form2Team)
              .then(teams.create)
              .then(teams.list)
              .then(teams.listTemp)
              .finally(vm.unsetLoading);
          }
        })
    }

    vm.exportData = function (data) {

      var csv = objToCsv(data.map(function(team) {
        return {
          Nome: team.teamName,
          Capitano: team.teamCaptain,
          Telefono: team.teamMobilePhone,
          "Orario Preferito": team.teamTime,
          "Giorno Preferito": team.teamPlayday.value,
          "Indirizzo Preferito": team.teamPlace
        }
      }));
      var blob = new Blob([csv], { type: 'text/csv' })
      saveAs(blob, "Squadre.csv");
    }

  }
})();

