(function() { 'use strict';

  angular.module('frispo')
    .controller('ScoreboardController', ScoreboardController);

  /** @ngInject */
  function ScoreboardController(models, $scope, events, $q, objToCsv) {

    var vm = this; vm.models = models; vm.loading = false;

    vm.getCurrentEvent = function() { return models.currentEvent || null; }

    // watch for changes in models.currentEvent and load
    // the calendar if current event has changed
    $scope.$watch(vm.getCurrentEvent, function(currentEvent) {
      if (!vm.loading  &&
          currentEvent && !currentEvent.scoreboard) {
        vm.setLoading(currentEvent)    // Set a spinning loader on the page
          .then(events.showScoreboard)        // Get the scoreboard for given event
          .finally(vm.unsetLoading);          // Remove the loader from the page
      }
    }, true);     // use object equality and not memory address

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }

    vm.exportData = function (data) {

      var csvObj = models.currentEvent &&
          models.currentEvent.evenType === 0 ?
        getCsvObj(data, false) :             // italiana
        data.map(function(scoresRound) {     // a gironi
          return getCsvObj(scoresRound.tournament, true);
        }).reduce(
          function(acc, value) { return acc.concat(value) });

      var blob = new Blob([ objToCsv(csvObj) ], { type: 'text/csv' })
      saveAs(blob, "Classifica.csv");
    }

    function getCsvObj(rowList, addRound) {

      return rowList.map(function(row) {

        var csvObj = {
          Squadra: row.rankTeamName,
          Punti: row.rankPoints,
          "Partite giocate": row.rankNumberPlayed,
          Vittorie: row.rankWon,
          Pareggi: row.rankSame,
          Sconfitte: row.rankLose,
          "Goal fatti": row.rankGoalScored,
          "Goal subiti": row.rankGoalConceded,
          "Diff reti": row.rankDiffReti,
          Disciplina: row.rankFoul
        }
        if (addRound) csvObj["Girone"] = row.rankNameGroups;
        return csvObj;
      });
    }
  }
})();

(function() { 'use strict';

  angular.module('frispo')
    .controller('PlayersController', PlayersController);

  /** @ngInject */
  function PlayersController(models, $scope, $q, $uibModal, teams, objToCsv) {

    var vm = this; vm.models = models; vm.loading = false;

    vm.getCurrentEvent = function() { return models.currentEvent || null; }

    // watch for changes in models.currentEvent and load
    // team info to fill up the team select options
    $scope.$watch(vm.getCurrentEvent, loadEventTeams, true);

    function loadEventTeams(currentEvent) {
      if (!vm.loading && currentEvent &&
         (!currentEvent.teams || !currentEvent.currentTeam)) {
        vm.setLoading(currentEvent.evenId)     // Set a spinning loader on the page
          .then(teams.list)                    // Get the event teams (API call) to fill up the select options
          .then(vm.updateTeam)                 // Update the match day
          .finally(vm.unsetLoading);           // Remove the loader from the page
      }
    }

    // Update the team (it's been selected now)
    vm.updateTeam = function () {
      // No current event selected, or no teams yet -> noop
      if(!models.currentEvent || !models.currentEvent.teams || models.currentEvent.teams.length === 0){
        return;
      }

      // No day selected - check if the event has just changed
      if(!models.currentEvent.currentTeam){
        // An event has been selected -> use it's first available team
        models.currentEvent.currentTeam = models.currentEvent.teams[0];
      }
      // Current day selected -> manually find and copy the real team from models (ng-options creates a new object)
      else{
        for (var i = 0; i < models.currentEvent.teams.length; i++) {
          if(models.currentEvent.teams[i].teamId === vm.models.currentEvent.currentTeam.teamId){
            vm.models.currentEvent.currentTeam = models.currentEvent.teams[i];
            break;
          }
        }
      }

      return vm
        .setLoading()
        .then(teams.listPlayers)
        .finally(vm.unsetLoading);
    }

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }

    vm.createNewPlayer = function () {
      $uibModal.open({
          templateUrl: 'app/pages/players/create/create.html',
          controller: 'PlayerCreateController',
          controllerAs: 'playerCreate',
          windowClass: 'modal-success'
        }).result
        .then(function (res) {
          if(angular.isDefined(res)){
            vm.setLoading(res)
              .then(teams.form2Player)
              .then(teams.createPlayer)
              .finally(vm.unsetLoading);
          }
        })
    }

    vm.exportData = function (data) {

    var csv = objToCsv(data.map(function(player) {
        return {
          Nome: player.playFirstname,
          Cognome: player.playSurname,
          Tessera: player.playTessera,
          Recapito: player.playRecapito
        }
      }));
      var blob = new Blob([csv], { type: 'text/csv' })
      saveAs(blob, "Giocatori.csv");
    };

    function init() {
      loadEventTeams(vm.getCurrentEvent());
    }
    init();
  }
})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .controller('RegisterController', RegisterController);

  /** @ngInject */
  function RegisterController(users, $state) {
    var vm = this;
    vm.user = vm.user || {};
    vm.privacy = vm.privacy || {};

    users.onLoggedOut(); // ensure the user is logged out

    vm.register = function (evt) {
      evt.preventDefault();
      for (var name in vm.privacy) {
        vm.user[name] = vm.privacy[name] ? 1 : 0;
      }
      if (vm.user.hasOwnProperty("flag_privacy")) {
        users.register(vm.user)
          .then(users.onLoggedIn)
          .then(function () { $state.go('main.access'); });
      }
    }
  }
})();

(function() {
    'use strict';

    angular
        .module('frispo')
        .controller('ProfileController', ProfileController);

    /** @ngInject */
    function ProfileController(models, users, $q, $uibModal) {
        var vm = this;
        vm.models = models;

        vm.userTypes = [{
                num: 0,
                name: 'Giocatore'
            },
            {
                num: 1,
                name: 'Organizzatore'
            }
        ];

        vm.userSubscriptions = [{
                num: 0,
                name: 'Gratuito'
            },
            {
                num: 1,
                name: 'Standard'
            },
            {
                num: 2,
                name: 'Premium'
            }
        ];

        vm.submit = function() {
            vm.setLoading(vm.form)
                .then(vm.updateForm)
                .then(users.form2User)
                .then(users.editUser)
                .then(users.info)
                .then(vm.initForm)
                .finally(vm.unsetLoading)
        }

        vm.deleteUser = function() {
            $uibModal.open({
                    templateUrl: 'app/pages/profile/destroy/destroy.html',
                    controller: 'ProfileDestroyController',
                    controllerAs: 'profileDestroy',
                    windowClass: 'modal-danger'
                }).result
                .then(function(res) {
                    if (res === true) {
                        users.deleteUser(vm.models.currentUser);
                    }
                })
        }

        vm.destroy = function() {
            vm.setLoading(vm.form)
                .then(users.deleteUser)
                .finally(vm.unsetLoading)
        }

        vm.setLoading = function(res) {
            vm.loading = true;
            return $q.resolve(res);
        }

        vm.unsetLoading = function(res) {
            vm.loading = false;
            return $q.resolve(res);
        }

        vm.initForm = function(res) {
            vm.form = {
                email: vm.models.currentUser.email,
                flagMarketing: vm.check(vm.models.currentUser.flagMarketing),
                flagProfilazione: vm.check(vm.models.currentUser.flagProfilazione)
            }
            return $q.resolve(res);
        }

        vm.updateForm = function(res) {
            if (vm.form.flagMarketing == 1)
                vm.form.flagMarketing = true;
            else
                vm.form.flagMarketing = false;
            if (vm.form.flagProfilazione == 1)
                vm.form.flagProfilazione = true;
            else
                vm.form.flagProfilazione = false;
            if (vm.form.flagMarketing && vm.form.flagProfilazione) {
                vm.form = {
                    email: vm.form.email,
                    flagMarketing: 1,
                    flagProfilazione: 1
                }
            } else if (!vm.form.flagMarketing && vm.form.flagProfilazione) {
                vm.form = {
                    email: vm.form.email,
                    flagMarketing: 0,
                    flagProfilazione: 1
                }
            } else if (vm.form.flagMarketing && !vm.form.flagProfilazione) {
                vm.form = {
                    email: vm.form.email,
                    flagMarketing: 1,
                    flagProfilazione: 0
                }
            } else {
                vm.form = {
                    email: vm.form.email,
                    flagMarketing: 0,
                    flagProfilazione: 0
                }
            }
            res = vm.form;
            return $q.resolve(res);
        }

        vm.check = function(flag) {
            if (flag == 1) {
                return true;
            } else {
                return false;
            }
        }

        vm.setLoading()
            .then(users.info)
            .then(vm.initForm)
            .then(vm.unsetLoading);

    }
})();
