(function() { 'use strict';

  angular.module('frispo')
    .controller('ImportScorersController', ImportScorersController);

  /** @ngInject */
  function ImportScorersController(models, events, $q, $uibModal, scorers, $interval, $scope) {

    var vm = this; vm.models = models; vm.loading = false;

    vm.getCurrentEvent = function() { return models.currentEvent || null; }

    // watch for changes in models.currentEvent and load
    // the calendar if current event has changed
    $scope.$watch(vm.getCurrentEvent, function(currentEvent) {
      if (!vm.loading  &&
          currentEvent && !currentEvent.listImportScorers) {
        vm.setLoading(currentEvent.evenId)    // Set a spinning loader on the page
          .then(scorers.listImport)           // Update the match day
          .finally(vm.unsetLoading);          // Remove the loader from the page
      }
    }, true);     // use object equality and not memory address

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }

    vm.importScorers = function () {
      $uibModal.open({
          templateUrl: 'app/pages/importScorers/create/create.html',
          controller: 'ImportScorersCreateController',
          controllerAs: 'importScorersCreate',
          windowClass: 'modal-success'
        }).result
        .then(function (res) {
          if(angular.isDefined(res)){
            var file = res.file;
            var format = res.format;
            scorers.getTokenS3()
            .then(function(data){
              var deferred = $q.defer();
              var credentials = {accessKeyId: data.data[0].keyid, secretAccessKey: data.data[0].secretid, sessionToken: data.data[0].session_token};
              AWS.config.update(credentials);
              AWS.config.sslEnabled = false;
              var bucket = new AWS.S3({params:{Bucket:'com.codeploy.frispo.utility.marcatori'}});
              if(file){
                var fileNameEscape = new Date().getTime() + "-" + file.name.replace(/\s+/g,'_');
                var params = {Key: ''+fileNameEscape , ContentType: file.type, Body: file };
                bucket.upload(params, function(err, data){
                  if(data){
                    var type = 0;
                    if (format=='cognome nome;squadra;goal') type = 1;
                    var payloadToImport = {
                      fileurl: "", // ??? data.fileurl ?,
                      filename: fileNameEscape,
                      importtype: type,
                      importfull: 1
                    }
                    scorers.importScorers(payloadToImport)
                    .then(scorers.listImport)
                    .then(function(){
                      deferred.resolve(data);
                    })
                  }else if(err){
                    deferred.reject(err);
                  }
                })
              }else{
                deferred.reject(false);
              }
              return deferred.promise;
            })
          }
        })
    }

    vm.listImportInterval = $interval(function(){
      if (vm.getCurrentEvent()) scorers.listImport()
    }, 30000);

    $scope.$on('$destroy', function() {
      $interval.cancel(vm.listImportInterval);
    });
  }
})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .controller('GiornateController', GiornateController);

  /** @ngInject */
  function GiornateController(models, $scope, events, matches, $q, $uibModal) {

    var vm = this; vm.models = models; vm.loading = false;

    vm.getCurrentEvent = function() { return models.currentEvent || null; }

    // watch for changes in models.currentEvent and load
    // the calendar if current event has changed
    $scope.$watch(vm.getCurrentEvent, function(currentEvent) {
      if (!vm.loading && currentEvent &&
         (!vm.models.currentEvent.currentDay ||
          !vm.models.currentEvent.currentDay.matches)) {
        vm.setLoading(currentEvent.evenId)    // Set a spinning loader on the page
          .then(vm.updateMatchDay)            // Update the match day
          .finally(vm.unsetLoading);          // Remove the loader from the page
      }
    }, true);     // use object equality and not memory address

    vm.updateMatchDay = function () {
      if(!models.currentEvent || !models.currentEvent.matchDays || models.currentEvent.matchDays.length === 0){
        return;
      }
      if(!models.currentEvent.currentDay){
        models.currentEvent.currentDay = models.currentEvent.matchDays[0];
      }
      else{
        for (var i = 0; i < models.currentEvent.matchDays.length; i++) {
          if(models.currentEvent.matchDays[i].matcDayId === vm.models.currentEvent.currentDay.matcDayId){
            vm.models.currentEvent.currentDay = models.currentEvent.matchDays[i];
            break;
          }
        }
      }
      return vm
        .setLoading()
        .then(matches.list)
        .finally(vm.unsetLoading);
    }

    vm.updateDayOfWeek = function (){
      $uibModal.open({
        templateUrl: 'app/pages/giornate/updateDay/create.html',
        controller: 'UpdateDayCreateController',
        controllerAs: 'updateDayCreate',
        windowClass: 'modal-danger'
      }).result
      .then(function (res) {
        if(angular.isDefined(res)){
          matches.updateDateMatchFromWeek()
          .then(function (){
            $uibModal.open({
              templateUrl: 'app/pages/giornate/updateDay/success.html',
              controller: 'UpdateDaySuccessController',
              controllerAs: 'updateDaySuccess',
              windowClass: 'modal-success'
            })
          })
          .catch(function(){
            $uibModal.open({
              templateUrl: 'app/pages/giornate/updateDay/error.html',
              controller: 'UpdateDayErrorController',
              controllerAs: 'updateDayError',
              windowClass: 'modal-danger'
            })
          })
        }
      })
    }

    vm.createNewDay = function () {
      $uibModal.open({
          templateUrl: 'app/pages/calendar/createDay/create.html',
          controller: 'DayCreateController',
          controllerAs: 'dayCreate',
          windowClass: 'modal-success'
        }).result
        .then(function (res) {
          if(angular.isDefined(res)){
            vm.setLoading(res)
              .then(matches.form2Day)
              .then(matches.createDay)
              .then(function (res) {
                models.currentEvent.currentDay = res;
                return res;
              })
              .then(vm.updateMatchDay)
              .finally(vm.unsetLoading);
          }
        })
    }

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }
  }
})();

