'use strict';

angular.module('frispo')
  /**
   * A wizard
   */
  .factory('Wizard', function () {
    var defaults = {
      totalSteps: 1,
      startFromStep: 1
    }

    return function Wizard (options) {
      this.options = angular.merge(defaults, options);

      this.totalSteps = this.options.totalSteps;
      this.currentStep = this.options.startFromStep;

      this.next = function () {
        if(this.currentStep < this.totalSteps){
          this.currentStep++;
        }
      }

      this.prev = function () {
        if(this.currentStep > 1){
          this.currentStep--;
        }
      }
    };
  });

'use strict';

/**
 * Utility per la conversione di un oggetto in una stringa in formato csv.
 * Codice copiato e incollato (it works !!) brutalmente da:
 *  https://medium.com/@polyismstudio/converting-json-to-csv-in-javascript-9ee95b4b5a54
 * */
angular.module('frispo')
  .factory('objToCsv', function () {

    return function(obj, opt) {
      if (!angular.isObject(obj)) return null;
      opt = opt || {};
      var scopechar = opt.scopechar || '/';
      var delimeter = opt.delimeter || ';';
      if (angular.isArray(obj) === false) obj = [obj];
      var curs, name, rownum, key, queue, values = [], rows = [], headers = {}, headersArr = [];
      for (rownum = 0; rownum < obj.length; rownum++) {
        queue = [obj[rownum], ''];
        rows[rownum] = {};
        while (queue.length > 0) {
          name = queue.pop();
          curs = queue.pop();
          if (curs !== null && angular.isObject(curs)) {
            for (key in curs) {
              if (curs.hasOwnProperty(key)) {
                queue.push(curs[key]);
                queue.push(name + (name ? scopechar : '') + key);
              }
            }
          } else {
            if (headers[name] === undefined) headers[name] = true;
            rows[rownum][name] = curs;
          }
        }
        values[rownum] = [];
      }
      // create csv text
      for (key in headers) {
        if (headers.hasOwnProperty(key)) {
          headersArr.push(key);
          for (rownum = 0; rownum < obj.length; rownum++) {
            values[rownum].push(rows[rownum][key] === undefined
              ? ''
              : JSON.stringify(rows[rownum][key]));
          }
        }
      }
      for (rownum = 0; rownum < obj.length; rownum++) {
        values[rownum] = values[rownum].join(delimeter);
      }
      return '"' + headersArr.join('"' + delimeter + '"') + '"\n' + values.join('\n');
    }
  })

