                if (angular.isDefined(formData.phone)) teamData.team_mobile_phone = formData.phone;
                if (angular.isDefined(formData.place)) teamData.team_place = formData.place;
                if (angular.isDefined(formData.playday)) teamData.team_playday = formData.playday.id;
                if (angular.isDefined(formData.round)) teamData.team_groups = formData.round;
                if(angular.isDefined(formData.temp)){
                    if(angular.isObject(formData.temp)){
                        teamData.team_temp = '' + formData.temp.id;
                    }
                    else{
                        teamData.team_temp = '' + formData.temp;
                    }
                }
                if (angular.isDefined(formData.teamUrlLogo)) teamData.team_url_logo = formData.teamUrlLogo;
                if (angular.isDefined(formData.desc)) teamData.team_description = formData.desc;
                return teamData;
