(function() {
  'use strict';

  angular
    .module('frispo')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig, $animateProvider, $locationProvider, ezfbProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Animate only elements with class "animate"
    $animateProvider.classNameFilter(/animate/);

    // Set options for toastr
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 5000;
    toastrConfig.positionClass = 'toast-top-center';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = false;

    //Removes '#' from the URL
    $locationProvider.html5Mode(true);
    $locationProvider.hashPrefix('!');

    // Configure facebook
    ezfbProvider.setLocale('it_IT');
    ezfbProvider.setInitParams({
      appId: '1025958327498737'
    });
  }

})();

